/* Miscellanoeous */
body,
html {
    overflow-x: hidden;
    padding-right: 0; // resets padding right added by Bootstrap modal
}

body {
    background: $content-bg;
}

*:-moz-full-screen,
*:-webkit-full-screen,
*:fullscreen *:-ms-fullscreen {
    overflow: auto;
}

.page-body-wrapper {
    min-height: calc(100vh - #{$navbar-height});
    @include display-flex();
    @include flex-direction(row);
    padding-left: 0;
    padding-right: 0;
    padding-top: $navbar-height;

    &.full-page-wrapper {
        min-height: 100vh;
        padding-top: 0;
        .main-panel {
            width: 100%;
            transition: none;
            margin-top: -$navbar-height;
            .content-wrapper {
                padding: 0;
                > div {
                    height: 100%;
                }
            }
        }
    }
}

.main-panel {
    transition: width $action-transition-duration
            $action-transition-timing-function,
        margin $action-transition-duration $action-transition-timing-function;
    width: calc(100% - #{$sidebar-width-lg});
    min-height: calc(100vh - #{$navbar-height});
    @include display-flex();
    @include flex-direction(column);
    @media (max-width: 991px) {
        margin-left: 0;
        width: 100%;
    }
    &.main-panel-only {
        transition: none;
    }
}

.content-wrapper {
    background: $content-bg;
    padding: 2.68rem 2.5rem;
    width: 100%;
    @include flex-grow(1);
    @media (max-width: 991px) {
        padding: 1.6rem 1.4rem 0 1.4rem;
    }
}

.container-scroller {
    overflow: hidden;
}

pre {
    background: color(gray-lighter);
    padding: 15px;
    font-size: 14px;
}

code {
    padding: 5px;
    color: theme-color(danger);
    font-family: $type-1;
    font-weight: 300;
    font-size: $default-font-size;
    border-radius: 4px;
}

.page-header {
    @extend .d-flex;
    @extend .justify-content-between;
    @extend .align-items-center;
    margin: 0 0 1.5rem 0;
    .breadcrumb {
        border: 0;
        margin-bottom: 0;
    }
}
.page-title {
    color: $page-title-color;
    font-size: 1.125rem;
    font-weight: 500;
    margin-bottom: 0;
    .page-title-icon {
        display: inline-block;
        width: 36px;
        height: 36px;
        border-radius: 4px;
        text-align: center;
        box-shadow: 0px 3px 8.3px 0.7px rgba(163, 93, 255, 0.35);
        i {
            font-size: 0.9375rem;
            line-height: 36px;
        }
    }
}
.dropdown-toggle {
    &.hide-carret {
        &:after {
            content: "";
            display: none;
        }
    }
}

.bg-transparent {
    background: transparent;
}
@each $color, $value in $social-colors {
    .text-#{$color} {
        @include text-color(social-color($color));
    }
}
