/* Bootstrap Progress */

.progress {
    @include border-radius(0px);
    height: 8px;
    .progress-bar {
        @include border-radius(0px);
    }

    &.progress-sm {
        height: 0.375rem;
    }
    &.progress-md {
        height: 11px;
    }
    &.progress-lg {
        height: 15px;
    }
    &.progress-xl {
        height: 18px;
    }
}
